<template>
	<div class="favgoods-container">
		<topNav class="topNav" :title="title" :isleftarrow="isleftarrow"></topNav>
		<van-swipe-cell v-for="item in list" :key="item.goodsId" :name="item.goodsId" :before-close="beforeClose">
			<van-card :desc="item.dateAdd+' 收藏'" :title="item.goodsName" :thumb="item.pic" class="goods-card" />
			<template #right>
				<van-button square text="删除" type="danger" class="delete-button" />
			</template>
		</van-swipe-cell>
	</div>
</template>

<script>
	import util from '@/common/util/util'
	import topNav from '../../components/topNav.vue'
	export default {
		data() {
			return {
				myfav: '',
				goodsId: [],
				list: [],
				title:'',
				isleftarrow:'',
			}
		},
		components: {
			topNav
		},
		created() {
			this.getFavList();
		},
		mounted() {
			this.title = this.$route.meta.title;
			this.isleftarrow = this.$route.meta.isleftarrow;
		},
		methods: {
			getFavList() {
				this.$request.post('/shop/goods/fav/list', {
					token: util.storage.get('token')
				}).then(res => {
					console.log(res);
					if (res.code !== 0) {
						return;
					}
					this.list = res.data;
				});
			},
			beforeClose({
				name,
				position,
				instance
			}) {
				switch (position) {
					case 'left':
					case 'cell':
					case 'outside':
						instance.close();
						break;
					case 'right':
						this.$dialog.confirm({
							message: '确定删除吗？',
						}).then(() => {
							// console.log(name);
							instance.close();
							this.$request.post('/shop/goods/fav/delete', {
								goodsId: name,
								token: util.storage.get('token')
							}).then(res => {
								console.log(res);
								if (res.code !== 0) {
									return;
								}
								this.$toast.loading({
									message: '正在删除',
									duration: 1000
								});
								setTimeout(this.getFavList, 1000); //延迟刷新				
							});
						}).catch(() => {
							// on cancel
							instance.close();
						});
						break;
				}
			},
		}
	}
</script>

<style lang="less" scoped>
	.favgoods-container {
		background-color: #fafafa;
		height: 100vh;
	}

	.goods-card {
		margin-top: 10px;
		background-color: white;
		text-align: start;
	}

	.delete-button {
		height: 100%;
	}
</style>
